nav ul li ul li:hover {    
background: rgba(249,0,77,.07);}


nav ul li ul li div:hover{font-size: 15px;    color: #f9004d; transform: translateY(-0px);}

nav ul ul {
display: none;
width: 47%;
position: absolute;
top: 60px;  
}

.bikedropcss{
position: absolute !important;
left: 20% !important;
}

/* Display Dropdowns on Hover */
nav ul li:hover > ul {
display:inherit;
min-width: 240px;
height: auto;
position: absolute;
top: 100%;
z-index: 90;
visibility: visible;
background-color: #fff;
box-shadow: 0 0 35px rgb(0 0 0 / 10%);
text-align: left;
padding: 12px 0;
border-radius: 4px; 
}

/* Fisrt Tier Dropdown */
nav ul ul li {
/* width: 125px; */
padding: 0px 15px;
height: 35px;
/* float: none; */
display: list-item;
/* position: relative; */
}
nav ul ul ul li {
position: relative;
top:-60px;
left:170px; 
}


/* Change ' +' in order to change the Dropdown symbol */
li > a:after { content:  ' +'; }
li > a:only-child:after { content: ''; }

