/* Media Queries */

@media all and (max-width : 768px) {


nav ul {
float: left;
padding: 0;
margin: 0;
list-style: none;
position: relative; 
left: 0;
line-height: 2;
}

.topheader ul {
float: none;
display: flex;
padding: 0px 20%;
}


.Footer ul li {
padding: 0px;
}

.Footer ul {
padding: 0px;
}

.Footerimg {
width: 20%;
}

.socialcss ul li {
padding: 20px 30px;
}
.socialcss ul li a{
color: #666;
}
#logo {
display: block;
padding: 0;
width: 100%;
text-align: center;
}

nav {
margin: 0;
}

.toggle + div,
.menu {
display: none;
}

.bikedropcss {
position: relative !important;
left: 0% !important;
top: 0px;
width: 60%;
}


/* Stylinf the toggle lable */
.toggle {
display: block;
text-align: center;
padding:14px 20px;	
color:#FFF;
font-size:17px;
text-decoration:none;
border:none;
}

[id^=drop]:checked + ul {
display: block;
}

nav ul li {
display: block;
width: 100%;
}

nav ul ul .toggle,
nav ul ul a {
padding: 0 40px;
}

nav ul ul ul a {
padding: 0 80px;
}

nav a:hover,
nav ul ul ul a {
background-color: #000000;
}

nav ul li ul li .toggle,
nav ul ul a,
nav ul ul ul a{
padding:14px 20px;	
color:#FFF;
font-size:17px; 
}


nav ul li ul li .toggle,
nav ul ul a {
background-color: #212121; 
}

nav ul ul {
float: none;
position:static;
color: #ffffff;
}

/* Hide menus on hover */
nav ul ul li:hover > ul,
nav ul li:hover > ul {
display: none;
}

/* Fisrt Tier Dropdown */
nav ul ul li {
display: block;
width: 100%;
}

nav ul ul ul li {
position: static;
}

}

@media all and (max-width : 330px) {

nav ul li {
display:block;
width: 94%;
}

}

@media all and (max-width : 1440px) {

nav ul {
position: relative;
left: 0%;
}

}
